<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <el-row>
            <el-col :span="8">
                <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList">
                    <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
                </el-input>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="orderlist" border stripe>
            <el-table-column label="" type="index"></el-table-column>
            <el-table-column label="" prop="order_number"></el-table-column>
            <el-table-column label="" prop="order_price"></el-table-column>
            <el-table-column label="" prop="pay_status">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.pay_status==='1'"></el-tag>
                    <el-tag type="danger" v-else></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" prop="is_send"></el-table-column>
            <el-table-column label="" prop="create_time">
                <template slot-scope="scope">
                    {{scope.row.create_time | dateFormat}}
                </template>
            </el-table-column>
            <el-table-column label="" width="130px">
                <template>
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showBox"></el-button>
                    <el-button size="mini" type="success" icon="el-icon-location" @click="showProgressBox"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
        <!--  -->
        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressClosed">
            <!--  -->
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="90px">
                <el-form-item label="/" prop="address1">
                    <el-cascader :options="cityData" v-model="addressForm.address1" :props="{expandTrigger: 'hover'}"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addAddress"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="progressVisible" width="50%">
            <!--  -->
            <el-timeline :reverse="false">
                <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
                    {{activity.context}}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityData from './citydata.js';
export default {
    data()
    {
        return {
            queryInfo:
            {
                query:'',
                pagenum:1,
                pagesize:10
            },
            total:0,
            orderlist:[],
            addressVisible:false,
            addressForm:
            {
                address1:[],
                address2:''
            },
            addressFormRules:
            {
                address1: 
                [
                    { required: true, message: '', trigger: 'blur' }
                ],
                address2: 
                [
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            cityData:cityData,
            progressVisible:false,
            //
            progressInfo:
            [
    {
      "time": "2018-05-10 09:39:00",
      "ftime": "2018-05-10 09:39:00",
      "context": ",,",
      "location": ""
    },
    {
      "time": "2018-05-10 08:23:00",
      "ftime": "2018-05-10 08:23:00",
      "context": "[]  95338",
      "location": ""
    },
    {
      "time": "2018-05-10 07:32:00",
      "ftime": "2018-05-10 07:32:00",
      "context": " []",
      "location": ""
    },
    {
      "time": "2018-05-10 02:03:00",
      "ftime": "2018-05-10 02:03:00",
      "context": "[], []",
      "location": ""
    },
    {
      "time": "2018-05-09 23:05:00",
      "ftime": "2018-05-09 23:05:00",
      "context": " []",
      "location": ""
    },
    {
      "time": "2018-05-09 21:21:00",
      "ftime": "2018-05-09 21:21:00",
      "context": "[], []",
      "location": ""
    },
    {
      "time": "2018-05-09 13:07:00",
      "ftime": "2018-05-09 13:07:00",
      "context": " ",
      "location": ""
    },
    {
      "time": "2018-05-09 12:25:03",
      "ftime": "2018-05-09 12:25:03",
      "context": "",
      "location": ""
    },
    {
      "time": "2018-05-09 12:22:24",
      "ftime": "2018-05-09 12:22:24",
      "context": "HLA()",
      "location": ""
    },
    {
      "time": "2018-05-08 21:36:04",
      "ftime": "2018-05-08 21:36:04",
      "context": "",
      "location": ""
    }
            ]
        }
    },
    created()
    {
        this.getOrderList();
    },
    methods:
    {
        async getOrderList()
        {
            const {data:res}=await this.$https.get('orders',{params:this.queryInfo});
            if(res.meta.status!==200)
            {
                return this.$message.error('');
            }
            this.total=res.data.total;
            this.orderlist=res.data.goods;
        },
        //pagesize
        handleSizeChange(newSize)
        {
            this.queryInfo.pagesize=newSize;
            this.getOrderList();
        },
        //
        handleCurrentChange(newPage)
        {
            this.queryInfo.pagenum=newPage;
            this.getOrderList();
        },
        //
        showBox()
        {
            this.addressVisible=true;
        },
        addressClosed()
        {
            this.$refs.addressFormRef.resetFields();
        },
        async showProgressBox()
        {
            //
            // const {data:res}=await this.$https.get('/kuaidi/1106975712662');
            // if(res.meta.status!==200)
            // {
            //     return this.$message.error('');
            // }
            // this.progressInfo=res.data;
            this.progressVisible=true;
        },
        addAddress()
        {

        }
    }
}
</script>

<style lang="less" scoped>
.el-cascader {
    width: 100%;
}
</style>